<template>
  <div :class="theme">
    <div class="highlight">
      <div class="highlight__inner" :style="color ? 'background-color: ' + color : ''">
        <div class="gauge" :class="'percent-'+percent + ' ' + 'former-'+former">
          <div class="dial">
            <div class="bar">
              <div class="circle circle-bg" :style="shadow ? 'color: ' + shadow : ''"></div>
              <div class="circle circle-cover"></div>
            </div>
            <div class="needle" v-if="!loading">
              <div class="inner">
              </div>
            </div>
          </div>
          <div role="status" class="text">
            <span>code</span>
            <span v-text="loading ? translations.unknown : code"></span>
          </div>
        </div>
        <div>
          <h2 v-text="translations.heading"></h2>
          <div v-html="this.translations.legend"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DrukteBarometer.vue',
  props: {
    color: String,
    shadow: String,
    offset: {
      type: Number,
      default: 0
    },
    translations: {
      type: Object,
      default: () => {
        return {
          heading: 'Volg live hoe druk het is in centrum Gent',
          unknown: 'ongekend',
          green: 'groen',
          orange: 'oranje',
          red: 'rood',
          legend: '<p><strong>Groen: </strong>het is momenteel rustig in het centrum.<br><strong>Oranje: </strong><br><strong>Rood:</strong> kom niet naar het centrum, het is te druk.</p>'
        };
      }
    },
    dataset: String,
    timeout: {
      type: Number,
      default: 600000
    },
    theme: {
      type: String,
      default: 'cs--cyan'
    }
  },
  data () {
    return {
      loading: true,
      percent: 1,
      former: 1
    };
  },
  computed: {
    code () {
      if (this.percent < 33) {
        return this.translations.green;
      }
      if (this.percent < 66) {
        return this.translations.orange;
      }
      return this.translations.red;
    }
  },
  methods: {
    fetchAndUpdate () {
      return this.fetch()
          .then(this.update)
          .then(() => this.loading = false)
          .catch(() => this.loading = true);
    },
    fetch () {
      if (!this.dataset) {
        return;
      }
      if (this.dataset === 'develop') {
        return new Promise(resolve => resolve(Math.floor(Math.random() * 4)));
      }

      let url = `https://data.stad.gent/api/records/1.0/search/?dataset=${this.dataset}`;
      return fetch(url)
          .then(response => response.json())
          .then(({records}) => records[0].fields.label_int);
    },
    update (v) {
      this.former = this.percent;
      let mappedValue = this.offset;

      switch (v) {
        case 0:
        case 1:
          mappedValue += 17;
          break;
        case 2:
          mappedValue += 50;
          break;
        case 3:
          mappedValue += 83;
          break;
      }

      if (mappedValue > 100) {
        this.percent = 100;
        return;
      }
      if (mappedValue < 0) {
        this.percent = 0;
        return;
      }

      this.percent = mappedValue;
    }
  },
  mounted () {
    this.fetchAndUpdate();
    setInterval(this.fetchAndUpdate, this.timeout);
  }
};
</script>

<style lang="scss">

$styleguide-dir: '../../node_modules/gent_styleguide/build/styleguide';
$font-dir: 'https://bartdelrue.github.io';

@import 'settings.scss';
