$dial-stroke-width: 1.5rem;
$start-pos: -120deg;
$dial-stroke-length: 80deg;

.highlight {

  padding-top: $circle-radius + 1rem;
  text-align: left;

  .highlight__inner {
    padding-top: 2rem + $circle-radius;
