    height: $circle-diameter + 1rem;
    padding-top: .5rem;
    position: absolute;
    margin: auto;
    border-radius: 50%;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: auto;
    left: 0;
  }

  @media (min-width: 768px) {

    padding-top: 0;

